@import url(https://fonts.googleapis.com/css2?family=Gluten:wght@200;300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@import-normalize;

:root {
  --black-color: rgb(40, 40, 40);
  --white-color: rgb(240, 240, 240);
}

.main-page, 
.leaderboard-page, 
.game-page {
  display: flex;
  flex-direction: column;
  background-color: rgb(40, 40, 40);
  background-color: var(--black-color);
  min-height: 100vh;
  min-width: 100vw;
  box-sizing: border-box;
}
.header {
    display: flex;
    flex-direction: row;
    position: relative;
}

.title {
    font-family: Gluten;
    font-size: 5rem;
    font-weight: 300;
    color: var(--white-color);
    padding-top: 10px;
    padding-bottom: 10px;
    margin: auto;
}

button {
    position: absolute;
    right: 40px;
    width: 150px;
    height: 40px;
    top: 0;
    bottom: 0;
    margin: auto 0;
    color: var(--white-color);
    background-color: var(--black-color);
    border: 2px solid var(--white-color);
    border-radius: 10px;
    font-size: large
}    

button:hover {
    border: 3px solid var(--white-color);
    width: 160px;
    height: 43px;
    right: 35px;
} 
.game-presentation {
    display: flex;
    flex-direction: column;
    color: var(---white-color);
    text-align:center;
}

.instructions{
    font-size: 1.5rem;
    margin: 20px auto;
    color:Lavender;
    line-height: 1.4;
}

.three-image-container {
    display: flex;
    flex-direction: row;
    height: 300px;
    margin: 25px 60px;
}

.image-xs-container{
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

.image-xs {
    display: flex;
    height: 100%;
    width: 100%;
    max-width: 30rem;
    margin: auto;
    object-fit: cover; 
    opacity: 0.75;
    background-color: var(---black-color);
}

.image-xs:hover {
    opacity: 0.95;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}     

.image-name{
   display: flex;
   justify-content: center;
   margin: 15px;
   font-size: x-large;
}

.space {
    display: flex;
    width: 100px;
    max-width: 100px;
}
.footer {
    display: flex;
    justify-content: center;
    margin-top: auto;
    width: 100%;
    height: 60px;
    font-size: x-large;
    color: var(--white-color);
    align-items: center;
    text-decoration: none;
}

a{
    text-decoration: none;
    color: var(--white-color);
}
.leaderboard {
    display: flex;
    flex-direction: column;
    margin: 20px auto;
}
 
.leaderboard .title{
    display: flex;
    justify-content: center;
    font-family: Gluten;
    font-size: 3rem;
    font-weight: 200;
    color: var(--white-color);
    padding-top: 10px;
    padding-bottom: 10px;
 }

.leaderboard .instructions {
    font-size: 1.5rem;
    margin: 20px auto;
    color: var(--white-color);
}

table {
    overflow-x:auto;
    color: var(--white-color);
    table-layout: fixed;
    width: 1000px;
    border-collapse: collapse;
    border: 3px solid rgb(105, 62, 105);
}
 
.row-table-header {
   height: 25px;
   padding: 50px;
   border: 3px solid rgb(105, 62, 105);
}   

.cell-table-header {
   font-size: 1.5rem;
   color: var(--white-color);
   padding: 15px;
}

.cell-table-body {
   font-size: 1.3rem;
   font-weight: 400;
   padding: 8px; 
}

 /* Dropdown Button */
.dropbtn {
   font-size: 1.5rem;
   color: var(--white-color);
   padding: 15px;
   border: none;
   background-color: var(--black-color)
}
  
/* The container <div> - needed to position the dropdown content */
.dropdown {
    position: relative;
}
  
/* Dropdown Content (Hidden by Default) */
.dropdown-content {
   display: none;
   position: absolute;
   left: -60px;
   background-color: rgb(150,150,150);
   color: white;
   min-width: 160px;
   box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
   z-index: 1;
   padding: 5px;
}
  
.item {
    padding: 5px;  
}

/* Change color of dropdown links on hover */
.dropdown-content .item:hover {
   background-color: #ddd;
}
  
/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
   display: block;
}

.icon {
   margin: auto;
}
.panel {
    flex-direction:column;
    width: 200px;
    color: var(--white-color);
}

h3 {
    color: var(--white-color);
    margin: 10px auto;
    text-align: center;
}

.item {
    border: 0.2px dotted grey;
}

.item img {
    display: flex;
    margin: 0 auto;
}

.item p {
    text-align: center;
    margin: 2px auto 7px auto;
}
.image-xl {
    display: flex;
    object-fit: cover; 
    width: 100%;
    user-select: none;
    -moz-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -ms-user-select: none;
}

.imageDiv {
    display: flex;
    border: 1px solid pink;
    position:absolute;
    min-width: calc(100% - 200px);
}

.popup {
    display: inline-block;
    border: 2px solid black;
    border-radius: 5px;
    background-color: silver;
    position: relative;
    width: 150px;
    height: 65px;
    z-index: 10;    
    color: black;
    padding: 10px;
    text-align: center;
    vertical-align: middle;
    line-height: 1.5;
}
.inputScore {
    display: flex;
    flex-direction: column;
    background-color:tan;
    border: 3px solid black;
    border-radius: 10px;
    background: linear-gradient(360deg,#949494 10%,#efefef 360%); 
    -webkit-background: linear-gradient(360deg,#949494 10%,#efefef 360%); 
    position: fixed;
    top: 50%;
    left: 50%;
    margin-top: -250px;
    margin-left: -450px;
    max-width: 900px;
    max-height: 500px;
    z-index: 15;     
    padding: 20px;
}

.popup1,
.popup2,
.popup3 {
    display: flex;
    font-size: 30px;
    text-align: center;
    margin: 8px auto;
}

.popup1 {
    font-weight: 600;
    font-size: 35px;
    margin: 25px auto;
}

.inputbox {
    display: flex;
    margin: 20px auto 0px auto;
}

label {
    display: flex;
    margin: 20px 20px 20px auto;
    font-size: 30px;
}

#inputPlayer {
    display: flex;
    margin: auto;
    width: 300px;
    font-size: 30px;
    padding: 5px;
}    

.buttonContainer {
    display: flex;
    margin: 30px auto;
    justify-content: center;
    text-align: center;
}

#saveBtn {
    margin: auto;
    position: static;
} 
.game {
    margin:0;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    max-width: 100vw;
}

.image-container {
    margin:0;
    box-sizing: border-box;
    display: flex;
    width: calc(100% - 200px);
}

